import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/Rx';
import { Observable } from 'rxjs';
import { MusicService } from '../shared/music.service';

@Component({
  selector: 'app-music',
  templateUrl: './music.component.html',
  styleUrls: ['./music.component.css']
})
export class MusicComponent implements OnInit {
  dataSource: Observable<any>;
  dataSource1: Observable<any>;
  dataSource2: Observable<any>;
  name: string;
  names = [];
  names2 = [];
  musicid: number;

  constructor(public http: Http, public musicsevice: MusicService) {
    //this.dataSource = this.http.get('/api/music').map(Response => Response.text());
    this.dataSource1 = this.http.get('/api/musicname').map(Response => Response.json());
    this.musicid = this.musicsevice.get(99);
    console.log(this.musicid);
  }
  ngOnInit() {
    this.dataSource1.subscribe(
      data => this.names = data
    );
  }

  /* 自定义函数*/



}





