import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  isActive = new Array();
  temp: number;

  click(index: number) {
    if (this.temp != index) {
      this.isActive[this.temp] = '';
    }
    this.isActive[index] = "active";
    this.temp = index;
  }
  ngOnInit() {

  }

}



